/* BUTTON CSS */
.lang-btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.lang-btn {
  font-size: 1rem;
  position: relative;
  width: 50px;
  height: 50px;
  border: none;
  background-color: #202020;
  color: white;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

#lang-btn-right {
  border-radius: 0;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}

.lang-btn::after {
  content: "";
  position: absolute;
  display: block;
  width: 50px;
  height: 50px;
  top: 0;
  left: 0;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: 0.1s;
  transition: 0.1s;
}

.lang-btn:hover {
  color: #202020;
  background-color: #dfd9e9;
  -webkit-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.lang-btn:active::after {
  opacity: 0.6;
  -webkit-animation: button__click 0.1s ease-out;
          animation: button__click 0.1s ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes button__click {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: #202020;
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    background-color: rgba(255, 255, 255, 0.4);
  }
}

@keyframes button__click {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: #202020;
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    background-color: rgba(255, 255, 255, 0.4);
  }
}

.hamburger__button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: auto;
  cursor: pointer;
}

.hamburger__meat {
  width: 50px;
  height: 5px;
  background-color: #202020;
  margin: 3px;
}

